$row-icon-size: 1.1rem;

.root {
  background-color: var(--light);
  transition: background-color var(--theme-change-transition);
}

.content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}

.reference {
  width: 16rem;

  &:not(:last-child) {
    padding-bottom: 1.5rem;
  }
}

.name {
  margin-bottom: 0.5rem;
}

.row {
  align-items: center;
  padding: 0.2rem 1rem;

  .rowIcon {
    margin-right: 0.6rem;
    width: $row-icon-size;
    height: $row-icon-size;
    color: var(--color-primary);
  }
}

